import React, { Component } from 'react';
import _ from 'lodash';
import './App.css';

class App extends React.Component {
  render() {    
    let sorted = this.state.data;
     
    const items = sorted.map((item)=>{
      return <Person data={ item } key={ item.id } />
    }); 
    
    return (
      <div className="container">
        <div className="row">
          <div className="row-rapper">
            { items } 
          </div>          
        </div>                
      </div>
    )
  }
}


class Person extends React.Component {
  render(){
    const { data, orderBy } = this.props;
    const input = categories; 
    const output = input.map((item)=>{
      return <div><small className={ orderBy === item ? "active" : null }>{item}:</small> {data[item] }</div>
    });
    return (    
      <div className="media">
        <div className="media-left">
          <img className="media-object" src={ data.img } alt={`${data.first_name} ${data.last_name}` } /> 
        </div>
        <div className="media-body">
          <h4 className="media-heading">
            <span className={ orderBy === "first_name" ? "active" : null }>{ data.first_name } </span> 
            <span className={ orderBy === "last_name" ? "active" : null }>{ data.last_name }</span>
          </h4>      
          {output}
        </div>
      </div>     
    )
  }
}
const categories = ["job"]
const data = [{"id":1,"first_name":"Robin","last_name":"Wheeler","img":"https://randomuser.me/api/portraits/women/1.jpg","job":"Analyst"},
{"id":2,"first_name":"Kathy","last_name":"Berry","img":"https://randomuser.me/api/portraits/women/2.jpg","job":"Engineer"},
{"id":3,"first_name":"Nicholas","last_name":"Armstrong","img":"https://randomuser.me/api/portraits/men/2.jpg","job":"Designer"},
{"id":4,"first_name":"Wanda","last_name":"Morris","img":"https://randomuser.me/api/portraits/women/3.jpg","job":"Producer"},
{"id":5,"first_name":"Mary","last_name":"Griffin","img":"https://randomuser.me/api/portraits/women/4.jpg","job":"Accountant"}];

export default App;



